body, body * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

ul{
    list-style: none;
}

header{
    background-color: rgb(141, 245, 245);
}

form{
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
}

h4{
    color: rgb(34, 34, 34);
    line-height: 1.5;
    margin-bottom: 1.5rem;
    text-align: center;
    font-weight: 500;
}

.form-group{
    margin-bottom: 0.8rem;
    display: flex;
}

.form-control{
    padding: 0.5rem 0.2rem;
    border: 1px solid rgb(211, 209, 209);
    border-radius: 5px;color: rgb(97, 96, 96);
    font-size: 1rem;
}

.form-control:focus{
    outline: 1px solid rgb(155, 154, 154);
}

input[type="submit"]{
    padding: 0.6rem;
    background-color: rgb(250, 250, 71);
    cursor: pointer;
    transition: 0.1s;
}

input[type="submit"]:hover{
    background-color: rgb(253, 253, 1);
}

.container{
    background-image: radial-gradient(18rem at 90% 20%, pink, transparent), radial-gradient(18rem at 10% 30%, rgb(129, 233, 236), transparent), radial-gradient(20rem at 40% 80%, rgb(123, 224, 119), transparent), radial-gradient(15rem at 70% 80%, rgb(243, 138, 52), transparent);
}

#cards{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-gap: 1rem;
    align-items: start;
}

.card{
    background-color: white;
    transform-style: preserve-3d;
    position: relative;
    cursor: pointer;
    border-radius: 5px;

    box-shadow: 0 0 2px 1px rgb(201, 199, 199);
    transition: .2s;
    transform: perspective(1200px) rotateY(var(--rotate-y,0)) translateY(var(--translate-y, 0));  
    /* 0 is default */
}

.card.flip{
    --rotate-y: 180deg;
}

.card:hover{
    --translate-y: -3px;
}

.back, .front{
    backface-visibility: hidden;
    position: absolute;
    padding: 1rem;
    border-radius: 5px;
    transition: 0.2s;
}

.front{
    display: flex;
    flex-flow: column nowrap;
}

.back{
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) rotateY(180deg);
}

.question{
    margin-bottom: 1rem;
    font-size: 0.9rem;
    color: rgb(53, 52, 52);
}

.options{
    margin-bottom: 1rem;
}

.options li{
    font-size: 0.8rem;
    margin-top: 0.5rem;
    color: rgb(83, 83, 83);
}

/* ----------------------------- media */
/* ----------------------------- 1 */
@media screen and (max-width:576px){
    html{
      font-size: 13px;
    }
    
    header{
        padding: 0.5rem;
    }

    .container{
        padding: 1rem 0.4rem;
    }

    .form-group{
        flex-flow: column nowrap;
        gap: 5px;
    }
    
    .form-group:last-of-type{
        max-width: 8rem;
    }

    label{
        flex: 1 1 30%;
    }
  }
  
  /* ----------------------------- 2 */
  @media screen and (min-width:576px) and (max-width:769px){
    html{
      font-size: 15px;
    }

    header{
        padding: 1rem;
    }
    
    .form-group{
        flex-flow: row nowrap;
        align-items: center;
        justify-content: flex-start;
    }

    .form-group:last-of-type{
        justify-content: flex-end;
    }
    
    .form-group > *:first-child{
        flex: 0 0 20%;
    }

    .form-group > *:nth-child(2){
        flex: 0 0 80%;        
    }

    .container{
        padding: 2rem 1rem;
    }
  }
  /* ----------------------------- 3 */
  @media screen and (min-width:769px){
    html{
      font-size: 16px;
    }

    header{
        padding: 1rem;
    }
  .form-group{
        flex-flow: row nowrap;
        align-items: center;
        justify-content: flex-start;
    }

    .form-group:last-of-type{
        justify-content: flex-end;
    }
    
    .form-group > *:first-child{
        flex: 0 0 20%;
    }

    .form-group > *:nth-child(2){
        flex: 0 0 80%;        

    }

    .container{
        padding: 2rem;
    }
  }